.titleBarBackground {
  width: 100%;
  background-color:rgb(0, 21, 39);
}

.titleBar {
  width: 90%;
  height: 60px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
  display: flex;
}
.searchBlock {
  width: 66%;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  align-items: center;
}
.searchBox {
  width: 100%;
  height: 35px;
  font-size: large;
  padding-left: 5px;
}

.pageContainer {
  margin-left: 90px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 500px);
}

.pageItem {
  width: 400px;
  height: 500px;
  margin: 20px;
  padding: 10px; 
}

.starRow {
  display:flex;
  align-items: center;
}

a {
  color: rgb(67, 127, 238);
  font-size: small;
}

a:hover {
  color: rgb(192, 0, 0);
}

.price {
  font-weight: bold;
  font-size: 130%;
  display: flex;
  vertical-align:text-top;
}
.price > .upper {
  font-weight: normal;
  font-size: 60%;
}

img {
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 400px;
}

.addItem {
  background-color: rgb(190, 190, 190);
  color: black;
  width: 75%;
  height: 20px;
  margin-left: 90px;
  margin-right: auto;
  margin-top: 10px;
  padding: 10px;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.addBox {
  width: 90%;
  height: 20px;
  padding-left: 5px;
  margin-right: 10px;
  margin-left: 10px;
}

.errorBar {
  background-color: rgba(255, 151, 151, 0.425);
  color: black;
  width: 75%;
  height: 20px;
  margin-left: 90px;
  margin-right: auto;
  margin-top: 10px;
  padding: 10px;
  border-radius: 5px;
  align-items: center;
}

.btnAdd {
  background-color: rgb(0, 192, 0);
  margin-left: 90px;
  color: white;
  font-weight: normal;
  font-size: 100%;
  width: 500px;
  height: 50px;
  border-radius: 10px;
  border-width: 0px;
  margin-top: 50px;
}

.btnAdd:hover {
  background-color: rgb(0, 170, 0);
  font-weight: bold;
}


.btnDelete {
  background-color: rgb(255, 0, 0);
  color: white;
  font-weight: normal;
  font-size: 100%;
  width: 400px;
  height: 50px;
  border-radius: 10px;
  border-width: 0px;
  margin-top: 50px;
}

.btnDelete:hover {
  background-color: rgb(240, 0, 0);
  font-weight: bold;
}
